$primary-color: #777;
$primary-font-size: 14px;
$primary-line-height: 22px;
$text-color: #e0e8e4;

.Footer-layout {
  font-size: $primary-font-size;
  line-height: $primary-line-height;
  font-family: Arial;
  color: $text-color;
}

.footer__banner-layout {
  background-color: $primary-color;
  margin-bottom: 20px;
}

.banner-layout__right-menu {
  height: 74px;
}


.footer {
  justify-content: space-between;
}




.menu__footer-right-menu-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 0;
  padding-right: 0;
}



.menu__footer-left-menu {
  list-style-type: none;
}




.menu__footer-center-menu {
  list-style-type: square;
}

